// Utility functions for mobile detection and interaction

export const isMobile = () => {
  // Verificar por user agent primeiro
  const userAgent = navigator.userAgent || navigator.vendor || window.opera;
  const isMobileUserAgent = /android|webos|iphone|ipad|ipod|blackberry|iemobile|opera mini/i.test(userAgent.toLowerCase());
  
  // Verificar por tamanho da tela
  const isMobileScreen = window.innerWidth <= 768;
  
  // Verificar por touch capability
  const isTouchDevice = 'ontouchstart' in window || navigator.maxTouchPoints > 0;
  
  console.log('Mobile detection:', {
    userAgent: userAgent,
    isMobileUserAgent,
    isMobileScreen,
    isTouchDevice,
    windowWidth: window.innerWidth,
    windowHeight: window.innerHeight
  });
  
  // Retornar true se qualquer um dos critérios for verdadeiro
  return isMobileUserAgent || isMobileScreen || isTouchDevice;
};

export const isMobileLandscape = () => {
  return isMobile() && window.innerWidth > window.innerHeight;
};

// Mobile interaction state management
export class MobileInteractionManager {
  constructor() {
    this.lastClickedFeature = null;
    this.clickTimeout = null;
    this.clickDelay = 500; // Aumentado para 500ms para dar mais tempo
    this.lastClickTime = 0;
  }

  handleClick(feature, onFirstClick, onSecondClick) {
    const currentTime = Date.now();
    const isMobileDevice = isMobile();
    
    console.log('MobileInteractionManager: handleClick chamado', {
      isMobile: isMobileDevice,
      feature: feature ? 'presente' : 'ausente',
      lastClickedFeature: this.lastClickedFeature ? 'presente' : 'ausente',
      timeSinceLastClick: currentTime - this.lastClickTime,
      clickDelay: this.clickDelay
    });

    if (!isMobileDevice) {
      // Desktop behavior - direct action
      console.log('MobileInteractionManager: Desktop - executando ação direta');
      onSecondClick(feature);
      return;
    }

    // Mobile behavior - two-click pattern
    if (this.lastClickedFeature === feature && (currentTime - this.lastClickTime) < this.clickDelay) {
      // Second click on same feature within timeout
      console.log('MobileInteractionManager: Segundo clique detectado - executando ação');
      this.clearTimeout();
      this.lastClickedFeature = null;
      this.lastClickTime = 0;
      onSecondClick(feature);
    } else {
      // First click or click on different feature
      console.log('MobileInteractionManager: Primeiro clique detectado');
      
      if (this.lastClickedFeature) {
        // Clear previous first click
        this.clearTimeout();
      }
      
      this.lastClickedFeature = feature;
      this.lastClickTime = currentTime;
      onFirstClick(feature);
      
      // Set timeout to clear first click
      this.clickTimeout = setTimeout(() => {
        console.log('MobileInteractionManager: Timeout expirado - limpando primeiro clique');
        this.lastClickedFeature = null;
        this.lastClickTime = 0;
      }, this.clickDelay);
    }
  }

  clearTimeout() {
    if (this.clickTimeout) {
      clearTimeout(this.clickTimeout);
      this.clickTimeout = null;
    }
  }

  reset() {
    console.log('MobileInteractionManager: Reset chamado');
    this.clearTimeout();
    this.lastClickedFeature = null;
    this.lastClickTime = 0;
  }
} 